import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './action'
import getters from './getters'

Vue.use(Vuex)

const state = {
	orderExtra: {},
	editAddress: {},
	istore: {},
	icart: {},
	locationInfo: {},
	erranderExtra: {},
	cart_animate: {}
}

export default new Vuex.Store({
	state,
	getters,
	actions,
	mutations,
})


