<template>
	<div id="loading">
		<div class="loading_container">
			<div class="load_img" :style="{backgroundPositionY: -(positionY%7)*60 + 'px'}">
			</div>
			<svg class="load_ellipse " xmlns="http://www.w3.org/2000/svg" version="1.1">
				<ellipse cx="26" cy="10" rx="26" ry="10" style="fill:#ddd;stroke:none;"></ellipse>
			</svg>
		</div>
	</div>
</template>

<script>
	export default {
		data(){
			return{
				positionY: 0,
				timer: null,
			}
		},
		mounted(){
			this.timer = setInterval(() => {
				this.positionY ++;
			}, 600)
		},
		beforeDestroy(){
			clearInterval(this.timer);
		}
	}
</script>

<style scoped>
	@keyframes load{
		0%   {transform: translateY(0px);}
		50%  {transform: translateY(-50px);}
		100% {transform: translateY(0px);}
	}
	@keyframes ellipse{
		0%   {transform: scale(1);}
		50%  {transform: scale(0.3);}
		100% {transform: scale(1);}
	}
	#loading{
		position: fixed;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		z-index: 100000000;
		background-color: #fff;
	}
	#loading .loading_container{
		position: fixed;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: 60px;
		height: 60px;
	}
	#loading .load_img{
		width: 100%;
		height: 100%;
		background: url(../../static/img/icon_loading.png) no-repeat 0 0;
		background-size: 60px auto;
		transform: translateY(0px);
		animation: load .6s infinite ease-in-out;
		position: relative;
		z-index: 11;
	}
	#loading .load_ellipse{
		position: absolute;
		width: 75px;
		height: 50px;
		top: 60px;
		left: 3px;
		z-index: 10;
		animation: ellipse .6s infinite ease-in-out;
	}
</style>



